import React, { useState, useEffect } from "react";
import {
  Container,
  Typography,
  Button,
  Divider,
  IconButton,
  Table,
  TableHead,
  TableRow,
  TableCell,
  TableBody,
  Dialog,
  DialogTitle,
  DialogContent,
  TextField,
  Snackbar,
  SnackbarContent,
  FormControl,
  InputLabel,
  Select,
  MenuItem,
} from "@material-ui/core";
import { connect } from "react-redux";
import moment from "moment";
import { withRouter } from "react-router-dom";
import { ArrowBack, Close, Add, AddAPhoto } from "@material-ui/icons";
import { red, green } from "@material-ui/core/colors";
import api from "../../api/api";
import { logout } from "../../actions";

const Advertisement = (props) => {
  const { logout, history } = props;
  const [formvalues, setformvalues] = useState({
    id: "",
    title: "",
    active: true,
    link: "",
    created: "",
  });

  const [wait, setwait] = useState(false);
  const [createdialog, setcreatedialog] = useState(false);
  const [imageDialog, setImageDialog] = useState(false);
  const [editdialog, seteditdialog] = useState(false);
  const [toast, settoast] = useState(false);
  const [message, setmessage] = useState(null);
  const [status, setstatus] = useState(null);
  const [data, setData] = useState([]);

  useEffect(() => {
    document.title = "News";
    fetchData();
  }, []);

  const fetchData = async () => {
    try {
      const response = await api.get(`/api/advertisement/list`, {
        headers: {
          Authorization: "Bearer " + localStorage.getItem("mom-admin"),
        },
      });
      return setData(response.data.data);
    } catch (err) {
      return;
    }
  };

  const submit = async (e) => {
    e.preventDefault();
    if (localStorage.getItem("mom-admin")) {
      try {
        setwait(true);
        const formData = new FormData();
        formData.append("title", formvalues.title);
        formData.append("active", formvalues.active);

        if (formvalues.link) {
          formData.append("document", formvalues.link, formvalues.link.name);
        } else {
          formData.append("document", null);
        }

        const response = await api.post(`/api/advertisement`, formData, {
          headers: {
            Authorization: "Bearer " + localStorage.getItem("mom-admin"),
          },
        });

        fetchData();
        setformvalues({
          id: "",
          title: "",
          active: true,
          link: "",
          created: "",
        });
        setstatus(green[700]);
        setmessage("Advertisement Published Successfully");
        setwait(false);
        setcreatedialog(false);
        return settoast(true);
      } catch (err) {
        setwait(false);
        setstatus(red[700]);
        setmessage("Something went wrong");
        return settoast(true);
      }
    } else {
      return logout();
    }
  };

  const submitEdit = async (e) => {
    e.preventDefault();
    if (localStorage.getItem("mom-admin")) {
      try {
        setwait(true);

        await api.patch(`/api/advertisement/${formvalues.id}`, formvalues, {
          headers: {
            Authorization: "Bearer " + localStorage.getItem("mom-admin"),
          },
        });
        setData(data.map((a) => (a.id === formvalues.id ? formvalues : a)));
        setformvalues({
          id: "",
          title: "",
          active: true,
          link: "",
          created: "",
        });
        seteditdialog(false);
        setstatus(green[700]);
        setmessage("Advertisement Updated Successfully");
        setwait(false);
        return settoast(true);
      } catch (err) {
        console.log(err);
        setwait(false);
        setstatus(red[700]);
        setmessage("Something went wrong");
        return settoast(true);
      }
    } else {
      return logout();
    }
  };

  const deletesubmit = async () => {
    if (localStorage.getItem("mom-admin")) {
      try {
        setwait(true);
        await api.delete(
          `/api/advertisement/${formvalues.id}`,

          {
            headers: {
              Authorization: "Bearer " + localStorage.getItem("mom-admin"),
            },
          }
        );
        setData(data.filter((a) => a.id !== formvalues.id));
        setformvalues({
          id: "",
          title: "",
          active: true,
          link: "",
          created: "",
        });
        seteditdialog(false);
        setstatus(green[700]);
        setmessage("Advertisement Deleted Successfully");
        setwait(false);
        return settoast(true);
      } catch (err) {
        setwait(false);
        setstatus(red[700]);
        setmessage("Something went wrong");
        return settoast(true);
      }
    } else {
      return logout();
    }
  };

  const renderlist = () => {
    if (data.length) {
      return (
        <Table size="small">
          <TableHead>
            <TableRow>
              <TableCell>Ad Title</TableCell>
              <TableCell align="center">Visible to Public</TableCell>
              <TableCell>Date of Publish</TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {data.map((a, index) => {
              return (
                <TableRow
                  hover
                  key={index}
                  style={{ cursor: "pointer" }}
                  onClick={() => {
                    setformvalues({ ...a });
                    seteditdialog(true);
                  }}
                >
                  <TableCell>
                    <Typography>{a.title}</Typography>
                  </TableCell>
                  <TableCell align="center">
                    <Typography
                      style={{
                        fontWeight: 500,
                        color: a.active ? "green" : "red",
                      }}
                    >
                      {a.active ? "Yes" : "No"}
                    </Typography>
                  </TableCell>
                  <TableCell>
                    <Typography>
                      {moment(a.created).format("DD-MM-YYYY")}
                    </Typography>
                  </TableCell>
                </TableRow>
              );
            })}
          </TableBody>
        </Table>
      );
    } else {
      return (
        <Typography align="center" variant="h6" style={{ marginTop: 300 }}>
          NO DATA TO DISPLAY!
        </Typography>
      );
    }
  };

  const renderdialog = () => (
    <Dialog
      fullWidth
      maxWidth="xs"
      open={createdialog}
      onClose={() => {
        setcreatedialog(false);
        setformvalues({
          id: "",
          title: "",
          active: true,
          link: "",
          created: "",
        });
      }}
    >
      <DialogTitle>Publish Advertisement</DialogTitle>
      <DialogContent dividers>
        <form onSubmit={submit}>
          <TextField
            required
            fullWidth
            variant="outlined"
            margin="normal"
            size="small"
            label="Advertisement Title"
            value={formvalues.title}
            onChange={(e) =>
              setformvalues({ ...formvalues, title: e.target.value })
            }
          />
          <FormControl
            variant="outlined"
            size="small"
            margin="normal"
            fullWidth
            required
          >
            <InputLabel id="select-active">Visible to Public</InputLabel>
            <Select
              label="Visible to Public"
              labelId="select-active"
              value={formvalues.active}
              onChange={(e) => {
                setformvalues({
                  ...formvalues,
                  active: e.target.value,
                });
              }}
            >
              <MenuItem value={true}>Yes</MenuItem>
              <MenuItem value={false}>No</MenuItem>
            </Select>
          </FormControl>
          <input
            style={{ display: "none" }}
            id="contained-button-file"
            multiple={false}
            type="file"
            onChange={(e) => {
              setformvalues({ ...formvalues, link: e.target.files[0] });
            }}
          />
          <label htmlFor="contained-button-file">
            <Button
              color="secondary"
              component="span"
              style={{ marginTop: 10 }}
              startIcon={<AddAPhoto />}
            >
              Choose File
            </Button>
          </label>
          <div
            style={{
              display: "flex",
              flexDirection: "column",
              alignItems: "center",
              fontWeight: 500,
              color: formvalues.link ? "green" : "red",
            }}
          >
            {formvalues.link ? "File Selected" : "No File Selected"}
          </div>
          <div
            style={{
              display: "flex",
              justifyContent: "flex-end",
              marginTop: 20,
            }}
          >
            <Button
              disabled={wait}
              type="submit"
              variant="contained"
              color="primary"
              style={{ marginRight: 10 }}
            >
              Submit
            </Button>
            <Button
              variant="contained"
              onClick={() => {
                setcreatedialog(false);
                setformvalues({
                  id: "",
                  title: "",
                  active: true,
                  link: "",
                  created: "",
                });
              }}
            >
              Cancel
            </Button>
          </div>
        </form>
      </DialogContent>
    </Dialog>
  );

  const rendereditdialog = () => (
    <Dialog
      fullWidth
      maxWidth="xs"
      open={editdialog}
      onClose={() => {
        seteditdialog(false);
        setformvalues({
          id: "",
          title: "",
          active: true,
          link: "",
          created: "",
        });
      }}
    >
      <DialogTitle>Edit Advertisement</DialogTitle>
      <DialogContent dividers>
        <form onSubmit={submitEdit}>
          <TextField
            required
            fullWidth
            variant="outlined"
            margin="normal"
            size="small"
            label="Advertisement Title"
            value={formvalues.title}
            onChange={(e) =>
              setformvalues({ ...formvalues, title: e.target.value })
            }
          />
          <FormControl
            variant="outlined"
            size="small"
            margin="normal"
            fullWidth
            required
          >
            <InputLabel id="select-active">Visible to Public</InputLabel>
            <Select
              label="Visible to Public"
              labelId="select-active"
              value={formvalues.active}
              onChange={(e) => {
                setformvalues({
                  ...formvalues,
                  active: e.target.value,
                });
              }}
            >
              <MenuItem value={true}>Yes</MenuItem>
              <MenuItem value={false}>No</MenuItem>
            </Select>
          </FormControl>

          <div
            style={{
              display: "flex",
              justifyContent: "flex-end",
              marginTop: 10,
            }}
          >
            <Button
              disabled={wait}
              type="submit"
              variant="contained"
              color="primary"
              style={{ marginRight: 10 }}
            >
              Update
            </Button>
            <Button
              disabled={wait}
              style={{ marginRight: 10 }}
              color="secondary"
              variant="contained"
              onClick={() => {
                if (
                  window.confirm(
                    `Are you sure you want to delete advertisement? You cannot recover once deleted!`
                  )
                ) {
                  return deletesubmit();
                }
              }}
            >
              Delete
            </Button>

            <Button
              variant="contained"
              onClick={() => {
                seteditdialog(false);
                setformvalues({
                  id: "",
                  title: "",
                  active: true,
                  link: "",
                  created: "",
                });
              }}
            >
              Cancel
            </Button>
          </div>
        </form>
      </DialogContent>
    </Dialog>
  );

  return (
    <Container>
      <Typography variant="h6">
        <IconButton
          edge="start"
          onClick={() => {
            history.goBack();
          }}
        >
          <ArrowBack />
        </IconButton>
        Advertisement Management
      </Typography>
      <div style={{ display: "block", marginTop: 8 }}>
        <Button
          variant="contained"
          color="secondary"
          startIcon={<Add />}
          onClick={() => setcreatedialog(true)}
        >
          Publish New Ad
        </Button>
      </div>
      <Divider style={{ marginTop: 20 }} />
      {renderlist()}
      {renderdialog()}
      {rendereditdialog()}
      {/* {renderImageDialog()} */}
      <Snackbar
        anchorOrigin={{
          vertical: "bottom",
          horizontal: "left",
        }}
        open={toast}
        autoHideDuration={3000}
        onClose={() => {
          settoast(false);
        }}
      >
        <SnackbarContent
          style={{ backgroundColor: status }}
          message={message}
          onClose={() => {
            settoast(false);
          }}
          action={
            <IconButton
              key="close"
              color="inherit"
              onClick={() => settoast(false)}
            >
              <Close />
            </IconButton>
          }
        ></SnackbarContent>
      </Snackbar>
    </Container>
  );
};

export default connect(null, { logout })(withRouter(Advertisement));
